
import axios from 'axios';
import PostList from '@/components/PostList'
import PostForm from '@/components/PostForm'
import MyDialog from "@/components/ui/MyDialog";
import MyButton from "@/components/ui/MyButton";
import MySelect from "@/components/ui/MySelect";
import MyInput from "@/components/ui/MyInput";
import MyPages from "@/components/ui/MyPages";

export default {
  components: {
    MyPages,
    MyInput,
    MySelect,
    MyButton,
    MyDialog,
    PostList,
    PostForm
  },
  data(){
    return {
      posts: [],
      dialogVisible: false,
      isPostsLoading: true,
      selectedSort: 'selected',
      selectedPostsPresentation: 'pagination',
      searchQuery: '',
      pageLimit: 10,
      pageNumber: 1,
      totalPages: 0,
      sortOptions: [
        {value: 'selected', name: 'Сортировка', disabled: true},
        {value: 'title', name: 'По названию'},
        {value: 'body', name: 'По содержимому'}
      ],
      postsPresentationOptions: [
        {value: 'pagination', name: 'Пагинация'},
        {value: 'infiniteScroll', name: 'Бесконечная прокрутка'}
      ]
    }
  },
  methods: {
    createPost(post){
      this.posts.push(post)
      this.dialogVisible = false
    },
    removePost(post){
      this.posts = this.posts.filter(item => item.id !== post.id)
    },
    showDialog(){
      this.dialogVisible = true
    },
    async fetchPosts(){
      try{
        this.isPostsLoading = true;
        const posts = await axios.get('https://jsonplaceholder.typicode.com/posts?', {
          params: {
            _limit: this.pageLimit,
            _page: this.pageNumber
          }
        })
        this.totalPages = Math.ceil(posts.headers['x-total-count'] / this.pageLimit)
        this.posts = posts.data
      } catch(e){
        alert('Error')
      } finally {
        this.isPostsLoading = false
      }
    },
    async loadMorePosts(){
      try{
        this.pageNumber += 1
        const posts = await axios.get('https://jsonplaceholder.typicode.com/posts?', {
          params: {
            _limit: this.pageLimit,
            _page: this.pageNumber
          }
        })
        this.totalPages = Math.ceil(posts.headers['x-total-count'] / this.pageLimit)
        this.posts = [...this.posts, ...posts.data]
      } catch(e){
        alert('Error')
      }
    }
  },
  computed: {
    sortedPosts() {
      return [...this.posts].sort((post1, post2) =>
          post1[this.selectedSort]
              ?.localeCompare(post2[this.selectedSort]))
    },
    sortedAndSearchedPosts(){
      if(this.searchQuery === ''){
        return this.sortedPosts
      } else {
        return this.sortedPosts.filter(post =>
            post.title.toLowerCase().includes(this.searchQuery.toLowerCase()))
      }
    }
  },
  watch:{
    pageNumber(){
      if (this.selectedPostsPresentation === 'pagination') {
        this.fetchPosts()
      }
    }
  },
  mounted(){
    this.fetchPosts()

    const options = {
      rootMargin: '0px',
      threshold: 1.0
    }
    const callback = (entries) => {
      if (entries[0].isIntersecting
          && this.pageNumber < this.totalPages
          && this.selectedPostsPresentation === 'infiniteScroll') {
        this.loadMorePosts()
      }
    };
    const observer = new IntersectionObserver(callback, options)
    observer.observe(this.$refs.observer)
  }
}
