

export default {
  data(){
    return {
      post: {
        title: '',
        body: ''
      }
    }
  },
  methods: {
    createPost(){
      this.post.id = Date.now()  // к посту, который уже заполненный лежит в data, добавляем id и можно передавать наверх
      this.$emit('createPost', this.post)
      this.post = {
        title: '',
        body: ''
      }
    }
  }
}
